import React from 'react'
import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    SettingOutlined
} from '@ant-design/icons';
import { getRoles } from '../../../function/fn';

export const userCol = ({ handleDelete, handleUserEdit,handleAccountEdit, comeBackUserRole, limit, page }) => {
    // let l = limit >= 20 ? limit/page : limit
    let no = ((page-1) * limit)


    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                    {no +=1 }
                    {
                        console.log(no)
                    }
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'username',
            key: 'username',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'firstName',
            key: 'firstName',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'lastName',
            key: 'lastName',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'roles',
            key: 'roles',
            width: 50,
            render: (text, record) => (
                <span onClick={()=> comeBackUserRole(record)} className="link" size="middle">
                    {
                        // getRoles(record?.roles)
                        record?.roles.map(load => (
                            load?.role + ", "
                        ))
                    }
                    
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 80,
        },
        {
            title: '',
            dataIndex: 'email',
            key: 'email',
            width: 100,
        },

        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 50,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    
                    <span className="link" onClick={() => handleAccountEdit(record)}><SettingOutlined /></span>
                    <span className="link" onClick={() => handleUserEdit(record)}><EditOutlined /></span>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}