import React from 'react'
import { Form, Modal, Input, Row, Col, Button, Select,DatePicker, message } from 'antd'
import { useMutation, useQuery } from '@apollo/client'
import { CREATE_PERSON_QUARANTINE } from '../../../graphql/quarantine'
import moment from 'moment'

const { Option } = Select

export default function AddSubQuarantine({ open, setOpen, quarantineId, peopleData, setRefetch }) {

    let [form] = Form.useForm()

    const [addPeopleToQuarantine,{data}]=useMutation(CREATE_PERSON_QUARANTINE,{
        onCompleted:({addPeopleToQuarantine})=>{
            if(addPeopleToQuarantine.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(addPeopleToQuarantine.message)
            }
            
        }
    })

    const onFinish = (values) => {
        console.log(values)
        addPeopleToQuarantine({variables:{
            ...values,
            date_in:moment(values.date_in).format(),
            date_out: values.date_out === undefined ? null : moment(values.date_out).format(),
            personalInfo:values.personalInfo,
            quarantineInfo:quarantineId,
            // others:values.others,
        }})

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addSubCase"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="personalInfo"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }} onChange={(e)=>console.log(e)}>
                                {peopleData.map((people)=>(
                                     <Option key={people?.id} value={people?.id}>{people?.lastName} {people?.firstName}</Option>
                                ))}
                               
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="personalTypes"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{ span: 24}}>
                        <Form.Item
                            name="in"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="date_in"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:2 }}>
                        <Form.Item
                            name="date_out"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{ span: 24}}>
                        <Form.Item
                            name="others"
                            // rules={[{ required: true, message: '!' }]}
                        >

                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
