import { gql } from '@apollo/client'

export const GET_ALL_PERSONINFO = gql`
query getPersonalInfoWithPagination($page:Int!,$limit:Int!,$keyword:String){
    getPersonalInfoWithPagination(page:$page,limit:$limit,keyword:$keyword){
        personalInfos{
          id
          firstName
          lastName
          age
          gender
          tel
          nationality
          occupation
          idCard
          profileImg
          village
          commune
          district
          interviewed
          province
          englishName
          patientId
          chronic
          direct
          other
          relapse
          relapseAt
          # vaccination
          createdAt
          updatedAt
          # currentState{
          #   confirm
          #   confirmedAt
          #   death
          #   deathAt
          #   recovered
          #   recoveredAt
          # }
        }
      paginator{
        slNo
        prev
        next
        perPage
        totalPosts
        totalPages
        currentPage
        hasPrevPage
        hasNextPage
        totalDocs
      }
    }
  }
`

