export const CREATE_NEW_PERSON = gql`
mutation createPersonalInfo(
        $covidVariant: String,
        $englishName:String,
        $patientId:String,
        $firstName:String,
        $lastName:String,
        $age: Int,
        $dob: DateTime,
        $gender:String,
        $other:String,
        $tel:String,
        $nationality:String,
        $occupation:String,
        $idCard:String,
        $village:String,
        $commune:String,
        $district:String,
        $province:String,
        $chronic:String,

        $date: DateTime,
        $times: Int,
        $vaccineType: String,
        $vacinatedAt: String,    

        $patientName: String,
        $patientCode: String,
        $relation: String,
        $direct: Boolean,
        $otherAffect: String
){
  createPersonalInfo(
      newInfo:{
        covidVariant: $covidVariant
        englishName:$englishName
        patientId:$patientId
        firstName:$firstName
        lastName:$lastName
        age: $age
        dob: $dob
        gender: $gender
        other: $other
        tel: $tel
        nationality: $nationality
        occupation:$occupation
        idCard: $idCard
        village: $village
        commune: $commune
        district: $district
        province: $province
        chronic:$chronic
        vaccination:{
          date: $date
          times: $times
          vaccineType:$vaccineType
          vacinatedAt: $vacinatedAt
        }
        affectedFrom: {
          patientName: $patientName
          patientCode: $patientCode
          relation: $relation
          direct: $direct
          otherAffect: $otherAffect
        }
    }
  ){
    personalInfo{
      id
      firstName
      lastName
    }
    response{
      success
      message
    }
  }
}
`

