export const RECORD_SAMPLETEST = gql`
mutation recordSampleTest(
    $reasonForTesting:String,
    $date:DateTime,
    $times: Int,
    $testLocation: String,
    $result: Boolean,
    $symptom: String,
    $other: String,
    $symptomStart: DateTime,
    $labFormCompletedBy: String,
    $specimentType: String,
    $laboratory: String,
    $covidVariant:String,
    $resultDate: DateTime,
    $personalInfoId:ID!,
    $testType:String
){
  recordSampleTest(sampleTest:{
    covidVariant:$covidVariant
    resultDate:$resultDate
    reasonForTesting:$reasonForTesting
    date:$date
    times: $times,
    testLocation: $testLocation
    result: $result
    symptom: $symptom,
    other: $other
    symptomStart: $symptomStart,
    labFormCompletedBy: $labFormCompletedBy,
    specimentType: $specimentType,
    laboratory: $laboratory
    testType:$testType
  },personalInfoId:$personalInfoId){
    message
    success
  }
}
`;

export const GET_PERSONALINFO_BY_ID = gql`
query getPersonalInfoById($id:ID!){
  getPersonalInfoById(id:$id){
    englishName
    patientId
    id
    firstName
    lastName
    age
    gender
    tel
    dob
    nationality
    occupation
    idCard
    profileImg
    village
    commune
    district
    province
    direct
    other
    relapse
    relapseAt
    createdAt
    updatedAt
    interviewed
    interviewedAt
    currentState{
      recovered
      death
      deathAt
      recoveredAt
      confirm
      confirmedAt
      covidVariant
    }
    sampleTest{
      id
      date
      times
      testLocation
      result
      symptom
      other
      reasonForTesting
      symptomStart
      labFormCompletedBy
      specimentType
      laboratory
      covidVariant
      resultDate
    }   
    travelOverCountryHistory{

      arriveDate
      fromCountry
      reasonForComing
      leavingDate
      toCountry
    }
    chronic
    covidVariant
    hospitalizations{
      hospitalName
    }
    historyWithin14days{
      id
      date
      locationName
      lat
      long
      description
      direct
    }
    quaranting{
      date_in
    }
    vaccination{
      date
      times
      vaccineType
      vacinatedAt
    }
    hospitalizations{
      date_in
      date_out
      hospitalName
      covidVariant
      coorporate
      description
    }
    affectedFrom{
      date
      patientName
      patientCode
      relation
      direct
      otherAffect
    }
  }
}

`;

