import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment'

export const locationCol = ({handleHistoryDelete,limit,page}) => {
    let no = 0
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 60,
            render: (text, record, i) => (
                <span>
                    {no+= 1}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
            width: 200,
            render: (text, record) => (
                <span>{console.log(record)}{moment.parseZone(record.date).format("DD MM YYYY")}</span>
            )
        },
        {
            title: '',
            dataIndex: 'locationName',
            key: 'locationName',
            width: 200,
        },
        {
            title: '',
            dataIndex: 'description',
            key: 'description',
            width: 200,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 80,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {/* <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link> */}
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleHistoryDelete(record) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}