import React, { useContext, useState } from 'react'
import { Modal, Form, Input, DatePicker, Row, Col, Button, message } from 'antd'
import { CaseController } from '../../../context/caseContext'
import { provinceData, districtData, communeData, villageData, genderData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage } from '../../../function/fn'
import { setPeopleLocation } from '../../../function/set'
import { CREATE_NEW_LOCATION} from '../../../graphql/location'
import { GET_ALL_CASES_NO_LIMIT } from '../../../graphql/case'
import { GET_ALL_LOCATION} from '../../../graphql/location'
import { useMutation,useQuery } from '@apollo/client'
import AddLocation from '../../location/modal/addLocation'
import { CREATE_NEW_HISTORYLOCATION } from '../../../graphql/historylocation'
import moment from 'moment'

export default function AddPeopleLocation({ open, setOpen, setRefetch, caseId, peopleId }) {

    
    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")
    const [locationData, setLocationData] = useState([])
    const [locationId, setLocationId] = useState("")
    const [location, setLocation] = useState({})
    const [openModal, setOpenModal] = useState(false)

    //const { caseDataDispatch } = useContext(CaseController)
    const [addHistoryWithin14days, { loading:createLoading}] = useMutation(CREATE_NEW_HISTORYLOCATION, {
        onCompleted: ({ addHistoryWithin14days }) => {
            setRefetch()
            message.success("")
        },
        onError: (error) => {
            console.log(error.message)
        }
    })

    const {data, loading,error} = useQuery(GET_ALL_LOCATION,{
        onCompleted:({allAffectedLocations})=>{
        console.log("location",allAffectedLocations)
        setLocationData(allAffectedLocations)
    }})

    let [form] = Form.useForm()

    const onFinish = (values) => {

        let index = locationData.findIndex(ele => ele.id === values.affectedLocation)

        if(locationId === 'new'){
            addHistoryWithin14days({
                variables:{
                    locationName: location.locationName, 
                    affectedLocation: location.id, 
                    date:moment(values.date),
                    lat: location.lat,
                    long: location.long,
                    description: values.description,
                    personalInfoId: peopleId,
                    direct: values.direct,
                }
            })
        }else {
            addHistoryWithin14days({
                variables:{
                    locationName: locationData[index].locationName, 
                    affectedLocation: values.affectedLocation, 
                    date:moment(values.date),
                    lat: locationData[index].lat,
                    long: locationData[index].long,
                    description: values.description,
                    personalInfoId: peopleId,
                    direct: values.direct,
                }
            })
        }

        console.log(location)   

        setOpen(false)
        form.resetFields()
        setLocationId("")
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };

    const setToCaseFn = (e) => {
        form.setFieldsValue({
            case: e
        });
    };

    const setToPeopleFn = (e) => {
        form.setFieldsValue({
            personalInfo: e
        });
    };

    const setLocationTypeFn = (e) => {
        form.setFieldsValue({
            type: e
        });
    };

    const setToLocationFn = (e) => {
        form.setFieldsValue({
            affectedLocation: e
        });
        if (e === "new") {
            setOpenModal(true)
        }

        setLocationId(e)
    };

    const callbackLocation = (e) => {
        // console.log("test",e)
        if(e === ""){
            setLocationId(e)
            form.setFieldsValue({
                affectedLocation: null
            })
        }else {
            setLocation(e)
            form.setFieldsValue({
                locationName: e.locationName
            })
        }
    }

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <AddLocation open={openModal} setOpen={setOpenModal} locationId={locationId} setLocationId={callbackLocation} />
            <Form
                form={form}
                name="addHistoryLocation"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{span: 11, offset: 0}}>
                        <Form.Item
                            name="affectedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <ListSelect type={5} data={locationData} title="" setValue={setToLocationFn} disabled={locationId === "new" ? true : false} />
                        </Form.Item>
                    </Col>

                    {
                        locationId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="locationName"
                                >
                                    <Input disabled={true} style={{backgroundColor: "white", color: "black"}} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }
                    
                    <Col xs={24} md={locationId === "new" ? {span: 11, offset: 0} : {span: 11, offset: 2}}>
                        <Form.Item
                            name="type"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <ListSelect type={1} data={[
                                {title: ""},
                                {title: ""}
                            ]} title="" setValue={setLocationTypeFn}/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={locationId === "new" ? {span: 11, offset: 2} : {span: 24}}>
                        <Form.Item
                            name="date"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>


                    <Col xs={24}>
                        <Form.Item
                            name="description"
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
