import { Typography,Image } from 'antd'
import Title from 'antd/lib/skeleton/Title';
import moment from 'moment';
import React, { Component } from 'react'
import HealthGov from '../../asset/health-gov.jpg'

export default class ReportLaboratory extends Component {
    render() {

        const labData = this.props.labFormData.labData;
        const personData = this.props.labFormData.personData;
        console.log(labData)
        return (
            <div style={{ padding: 30}} className="labform">
                <div
                    style={{
                        position: "relative",
                        height: 80,
                        // fontSize:12
                    }}
                >
                    <div
                        style={{
                            position: "absolute",
                            bottom: 0,
                            left: -20,
                            // backgroundColor: "#C5E0B4",
                            padding: 10,    
                            width:95
                        }}
                    >
                        <Image width={"100%"} src={HealthGov} preview={false} />
                    </div>
                    <center>
                        <Typography.Title level={5} className="HeaderFont">
                            <br />
                            Laboratory Request Form
                        </Typography.Title>
                    </center>
                    <div
                        style={{
                            position: "absolute",
                            bottom: 15,
                            right: 0,
                            // backgroundColor: "#C5E0B4",
                            padding: 10
                        }}
                    >
                        <span className="stamp-state">
                            
                            {personData?.currentState?.confirm ? "Positive":"Negative"}
                        </span>
                    </div>
                </div>

                <table border={1} className="table">
                    <tbody>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Health Facility</li>
                                </ul>
                            </td>
                            <td>

                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Date/</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{moment.parseZone(labData?.date).format("DD MM YYYY")}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Completed by</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{labData?.labFormCompletedBy}</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Telephone/</li>
                                </ul>
                            </td>
                            <td>

                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Reasons for testing</li>
                                    <li></li>
                                </ul>
                            </td>
                            <td
                                colSpan={3}
                            >
                                <ul>
                                    <li>{labData?.reasonForTesting}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan={4}>/Patient information/</td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Patient Name/</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li><b>{personData?.lastName+" "+personData?.firstName}</b></li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Patient ID</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{personData?.patientId}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Sex /</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{personData?.gender}</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Age /</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{personData?.age} </li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Nationality /</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{personData?.nationality}</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Telephone/</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{personData?.tel}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Address /</li>
                                </ul>
                            </td>
                            <td colSpan={3}>
                                <ul>
                                    <li>........................................</li>
                                    <li>
                                         {personData?.village ==="" ? "................":personData?.village}
                                         {personData?.commune ==="" ? "................":personData?.commune}
                                         {personData?.district ==="" ? "................":personData?.district}
                                         {personData?.province ==="" ? "................":personData?.province}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Clinical Symptoms</li>
                                    <li></li>
                                </ul>
                            </td>
                            <td colSpan={3}>
                                <ul>
                                    <li>{labData?.symptom}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Date of onset /</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{ labData?.symptomStart === null || labData?.symptomStart=== undefined ? "": moment.parseZone(labData?.symptomStart).format("DD MM YYYY")}</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>-?</li>
                                    <li>History of COVID-19 positive?</li>
                                    <li></li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li> { labData?.date === null || labData?.date=== undefined ? "": moment.parseZone(labData?.date).format("DD MM YYYY")}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td rowSpan={3}>
                                <ul>
                                    <li></li>
                                    <li>Travel history/</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Travel history/</li>
                                </ul>
                            </td>
                            <td>

                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Date of arrival</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li>/</li>
                                    <li>Passport No /</li>
                                </ul>
                            </td>
                            <td>

                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Seat No/ </li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                  
                            </td>
                            <td colSpan={3}>

                            </td>
                        </tr>
                        <tr>
                            <td colSpan={5}>/Laboratory</td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Place of collection</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{labData?.testLocation}</li>
                                </ul>
                                
                            </td>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Date of collection</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{ labData?.date === null || labData?.date=== undefined ? "": moment.parseZone(labData?.date).format("DD MM YYYY")}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Type of specimen</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{labData?.specimentType}</li>
                                </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>
                                     </li>
                                    </ul>
                            </td>
                            <td>
                                <ul>
                                    <li>{labData?.laboratory}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li>()</li>
                                    <li>No of Sample (circle)</li>
                                </ul>
                            </td>
                            <td colSpan={3}>
                                <ul>
                                    <li> {labData?.times}</li>
                                </ul>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <ul>
                                    <li></li>
                                    <li>Vaccination Status</li>
                                </ul>
                            </td>
                            <td colSpan={2}>
                                <ul>
                                    {personData?.vaccination?.map((v,index)=>(
                                        <li> {v.times}   { v.date === null || v.date=== undefined ? "": moment.parseZone(v.date).format("DD MM YYYY")}</li>
                                    ))
                                    }
                                </ul>
                            </td>
                            <td>

                            </td>
                        </tr>
                    </tbody>
                </table>
                <p>
                    *IPC: Institute Pasteur of Cambodia; NIPH: National Institute of Public Health; PKML: 
                    Preah Ketmealea Hospital; SRH: Siem Reap Referral Hospital; MKB: Mongkol Borey Hospital; 
                    BTB: Battambang Provincial Hospital; TKV: Takeo Provincial Hospital; NBTC: National Blood 
                    Transfusion Center; SHV: Sihanouk Ville Provincial Hospital
                </p>
                <p>
                     / Signature: 
                </p>
            </div>
        )
    }
}
