import React, { useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Row,
  Col,
  Button,
  Select,
  Divider,
  DatePicker,
  message,
  Checkbox,
} from "antd";
import {
  provinceData,
  districtData,
  communeData,
  villageData,
  genderData,
  nationalityData,
} from "../../../context/headerContext";
import { ListSelect } from "../../../static/own-comp";
import {
  convertToCommune,
  convertToDistrict,
  convertToVillage,
  convertPersonalToSelect,
} from "../../../function/fn";
import { useQuery, useMutation } from "@apollo/client";
import { GET_ALL_CASES_NO_LIMIT } from "../../../graphql/case";
import { UPDATE_PERSON_BY_ID } from "../../../graphql/people";
import {
  DELETE_PERSONALINFO_BY_ID,
  GET_ALL_PERSONINFO,
  GET_ALL_PERSONINFO_SAMPLE,
} from "../../../graphql/people";
import moment from "moment";

const { Option } = Select;

export default function EditPeople({
  open,
  setOpen,
  personId,
  personalData,
  setRefetch,
  personalInfoId,
  setPersonalInfoId,
}) {
  // console.log(personalData)
  let [form] = Form.useForm();

  const [province, setProvince] = useState("");
  const [district, setDistrict] = useState("");
  const [commune, setCommune] = useState("");
  const [caseId, setCaseId] = useState("");
  const [caseData, setCaseData] = useState({});
  const [allCases, setAllCases] = useState([]);

  const [patientId, setPatientId] = useState("None");

  const [openModal, setOpenModal] = useState(false);

  const [interviewed, setInterviewed] = useState(false);

  console.log(personalData)

  //for select people
  const { data: personalInfo } = useQuery(GET_ALL_PERSONINFO_SAMPLE);
  const personalInfoData = personalInfo?.allPersonalInfos;

  const [updatePersonalInfo, { loading: loadingCreate, error: errorCreate }] =
    useMutation(UPDATE_PERSON_BY_ID, {
      onCompleted: ({ updatePersonalInfo }) => {
        setRefetch();
        message.success("");
      },
      onError: (error) => {
        console.log(error.message);
      },
    });

//     date: null
// direct: true
// otherAffect: null
// patientCode: null
// patientName: null
// relation: null

  const { data, loading, error } = useQuery(GET_ALL_CASES_NO_LIMIT, {
    onCompleted: ({ allCases }) => {
      // console.log(allCases)
      setAllCases(allCases);
    },
  });

  useEffect(() => {
    if (personalData !== undefined) {
    //   console.log(personalData);
      form.setFieldsValue({
        ...personalData,
        dob:  personalData.dob ===null || personalData.dob === undefined ? null : moment(personalData.dob),
        patientCode: personalData.affectedFrom.patientCode,
        patientName: personalData.affectedFrom.patientName,
        direct: personalData.affectedFrom.direct,
        relation: personalData.affectedFrom.relation,
        otherAffect: personalData.affectedFrom.otherAffect,
        date: personalData.affectedFrom.date === null || personalData.affectedFrom.date===undefined ? null : moment(personalData.affectedFrom.date),
      });
      setInterviewed(personalData?.interviewed);
      setProvince(personalData?.province);
      setDistrict(personalData?.district);
      setCommune(personalData?.commune);
    }
  }, [personalData]);

  const onFinish = (values) => {
    console.log("Success:", {
      ...values,
      age: parseInt(values.age),
      village: values.village === undefined ? "" : values.village,
      commune: values.commune === undefined ? "" : values.commune,
      district: values.district === undefined ? "" : values.district,
      province: values.province === undefined ? "" : values.province,
      id: personId,
      dob: moment(values.dob),
      date: moment(values.date),
      interviewedAt: moment(values.interviewedAt),
    });
    // illness: values.illness
    updatePersonalInfo({variables: {
        ...values,
        age: parseInt(values.age),
        village: values.village === undefined ? "" : values.village,
        commune: values.commune === undefined ? "" : values.commune,
        district: values.district === undefined ? "" : values.district,
        province: values.province === undefined ? "" : values.province,
        id: personalData?.id,
        dob: moment(values.dob),
        date: moment(values.date),
        interviewedAt: moment(values.interviewedAt),
    }});

    // if(caseId === 'new'){
    //     createPersonalInfo({ variables: {...setAddPeople(values), case: caseData.id} })
    // }else {

    // createPersonalInfo({ variables: setAddPeople(values) })
    // }

    setOpen(false);
    form.resetFields();
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const setPatientCodeFn = (e) => {
    let index = personalInfoData.findIndex((ele) => ele.id === e);

    if (index !== -1) {
      form.setFieldsValue({
        patientCode: e,
        patientName:
          personalInfoData[index].lastName +
          " " +
          personalInfoData[index].firstName,
      });
    } else {
      form.setFieldsValue({
        patientCode: e,
        patientName: "",
      });
    }

    setPatientId(e);
  };

  const setToGenderFn = (e) => {
    form.setFieldsValue({
      gender: e,
    });
  };

  const setToNationalityFn = (e) => {
    form.setFieldsValue({
      nationality: e,
    });
  };

  const setToProviceFn = (e) => {
    form.setFieldsValue({
      province: e,
      district: null,
      commune: null,
      village: null,
    });

    setProvince(e);
    setDistrict("");
    setCommune("");
  };

  const setToDistrictFn = (e) => {
    form.setFieldsValue({
      district: e,
      commune: null,
      village: null,
    });

    setDistrict(e);
    setCommune("");
  };

  const setToCommuneFn = (e) => {
    form.setFieldsValue({
      commune: e,
      village: null,
    });

    setCommune(e);
  };

  const setToVillageFn = (e) => {
    form.setFieldsValue({
      village: e,
    });
  };

  const setToCaseFn = (e) => {
    form.setFieldsValue({
      case: e,
    });
    if (e === "new") {
      setOpenModal(true);
    }

    setCaseId(e);
  };

  const callbackCase = (e) => {
    if (e === "") {
      setCaseId(e);
      form.setFieldsValue({
        case: null,
      });
    } else {
      setCaseData(e);
      form.setFieldsValue({
        caseName: e.caseName,
      });
    }
  };

  return (
    <Modal
      title=""
      visible={open}
      onOk={() => setOpen(false)}
      onCancel={() => setOpen(false)}
      footer={null}
    >
      <Form
        form={form}
        name="addPeople"
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Row>
          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="idCard"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="patientId"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>
          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="lastName"
              rules={[{ required: true, message: "!" }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="firstName"
              rules={[{ required: true, message: "!" }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 0 }}>
            <Form.Item
              name="englishName"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="dob"
              // rules={[{ required: true, message: '!' }]}
            >
              <DatePicker
                style={{ width: "100%" }}
                placeholder=""
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="gender"
              rules={[{ required: true, message: "!" }]}
            >
              <ListSelect
                type={0}
                data={genderData}
                title=""
                setValue={setToGenderFn}
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="age"
              rules={[{ required: true, message: "!" }]}
            >
              <Input placeholder="" type="number" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="occupation"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>
          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="tel"
              rules={[{ required: true, message: "!" }]}
            >
              <Input placeholder="" addonBefore="+855" type="number" />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="nationality"
              rules={[{ required: true, message: "!" }]}
            >
              <ListSelect
                type={0}
                data={nationalityData}
                title=""
                setValue={setToNationalityFn}
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="province"
              rules={[{ required: true, message: "!" }]}
            >
              <ListSelect
                type={1}
                data={provinceData}
                title="/"
                setValue={setToProviceFn}
              />
            </Form.Item>
          </Col>

          {province === "" ? (
            <>
              <Col xs={24} md={{ span: 11 }}>
                <Form.Item
                  name="district"
                  rules={[
                    { required: true, message: "!" },
                  ]}
                >
                  <ListSelect
                    type={0}
                    data={convertToDistrict(districtData)}
                    title="/"
                    setValue={setToDistrictFn}
                    disabled={province !== "" ? true : false}
                  />
                </Form.Item>
              </Col>
              <Col xs={24} md={{ span: 11, offset: 2 }}>
                <Form.Item
                  name="commune"
                  rules={[
                    { required: true, message: "!" },
                  ]}
                >
                  <ListSelect
                    type={1}
                    data={convertToCommune(district, communeData)}
                    title="/"
                    setValue={setToCommuneFn}
                    disabled={
                      district === "" || district === null ? true : false
                    }
                  />
                </Form.Item>
              </Col>
              <Col xs={24} md={{ span: 24 }}>
                <Form.Item
                  name="village"
                  rules={[
                    { required: true, message: "!" },
                  ]}
                >
                  <ListSelect
                    type={1}
                    data={convertToVillage(commune, villageData)}
                    title=""
                    setValue={setToVillageFn}
                    disabled={commune === "" || commune === null ? true : false}
                  />
                </Form.Item>
              </Col>
            </>
          ) : null}

          {/* <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

          {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vaccinated"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col> */}

          <Col xs={24} md={{ span: 24, offset: 0 }}>
            <Form.Item
              name="chronic"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          {/* <Divider></Divider>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="vaccineType"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value="SINOPHARM">SINOPHARM</Option>
                                <Option value="SINOVAC">SINOVAC</Option>
                                <Option value="ASTRAZENECA">ASTRAZENECA</Option>
                                <Option value="JOHNSON JOHNSON">JOHNSON JOHNSON</Option>
                                <Option value="PFIZER">PFIZER</Option>
                                <Option value="Other">Other</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vacinatedAt"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

          <Divider></Divider>
          <Col xs={24} md={{ span: 11, offset: 0 }}>
            <Form.Item
              name="patientCode"
              // rules={[{ required: true, message: '!' }]}
            >
              <ListSelect
                type={7}
                data={convertPersonalToSelect(personalInfoData)}
                title=""
                setValue={setPatientCodeFn}
              />
            </Form.Item>
          </Col>
          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="patientName"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input
                placeholder=""
                disabled={patientId === "" ? false : true}
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11 }}>
            <Form.Item
              name="date"
              // rules={[{ required: true, message: '!' }]}
            >
              <DatePicker
                style={{ width: "100%" }}
                placeholder=""
              />
            </Form.Item>
          </Col>

          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="direct"
              // rules={[{ required: true, message: '!' }]}
            >
              <Select placeholder="">
                <Option value={true}></Option>
                <Option value={false}></Option>
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={{ span: 11, offset: 0 }}>
            <Form.Item
              name="relation"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>
          <Col xs={24} md={{ span: 11, offset: 2 }}>
            <Form.Item
              name="otherAffect"
              // rules={[{ required: true, message: '!' }]}
            >
              <Input placeholder="" />
            </Form.Item>
          </Col>

          {/* <Divider></Divider>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="case"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={4} data={allCases} title="" setValue={setToCaseFn} disabled={caseId === "new" ? true : false} />
                        </Form.Item>
                    </Col>

                    {
                        caseId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="caseName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    <Col xs={24} md={caseId === "new" ? { span: 24 } : { span: 11, offset: 2 }}>
                        <Form.Item
                            name="direct"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>

                            </Select>
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"

                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

          <Divider></Divider>
          <Col xs={24} md={{ span: 11 }}>
            <Form.Item name="interviewed" valuePropName="checked">
              <Checkbox onChange={(e) => setInterviewed(e.target.checked)}>
                
              </Checkbox>
            </Form.Item>
          </Col>

          {interviewed ? (
            <Col xs={24} md={{ span: 11, offset: 2 }}>
              <Form.Item
                name="interviewedAt"
                // rules={[{ required: true, message: '!' }]}
              >
                <DatePicker
                  style={{ width: "100%" }}
                  placeholder=""
                />
              </Form.Item>
            </Col>
          ) : null}

          <Col xs={24}>
            <Button htmlType="submit" type="primary" style={{ width: "100%" }}>
              
            </Button>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
}
