import {gql} from '@apollo/client'

export const CREATE_NEW_HISTORYLOCATION = gql`
mutation addHistoryWithin14days(
  $personalInfoId:ID!,
  $locationName: String,
  $affectedLocation: ID!,
  $date: DateTime,
  $lat: Float,
  $long: Float,
  $description: String,
  $direct: Boolean
){
    addHistoryWithin14days(createLocation:{
      locationName: $locationName
      affectedLocation: $affectedLocation
      date: $date
      lat: $lat
      long: $long
      description:$description
      direct:$direct
    },personalInfoId:$personalInfoId){
      message
      success
    }
  }
`

export const GET_HISTORYLOCATION_BY_PERSON = gql`
query getHistoryLocationByPersonalInfoId($personalId:ID!){
	getHistoryLocationByPersonalInfoId(personalId:$personalId){
    affectedLocationId{
      id
      affectedLocationName
    }
    case{
      id
    }
    personalInfo{
      id
    }
    date
    type
  }
}
`