import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const sampleCol = ({handleSampleTestDelete,limit,page}) => {
    let no = ((page-1) * limit)
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 30,
            render: (text, record, i) => (
                <span>
                    {no += 1}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'patientId',
            key: 'patientId',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 80,
            render: (text, record) => (
                <span>
                    {record?.lastName+" "+record?.firstName}
                    {/* {console.log(record)} */}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'confirmedAt',
            key: 'confirmedAt',
            width: 70,
            render: (text, record) => (
                <span>
                    {record?.currentState?.confirmedAt === null ? "" : moment.parseZone(record?.currentState?.confirmedAt).format("DD MM YYYY")}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'deathAt',
            key: 'deathAt',
            width: 70,
            render: (text, record) => (
                <span>
                    {record?.currentState?.deathAt === null ? "" : moment.parseZone(record?.currentState?.deathAt).format("DD MM YYYY")}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'times',
            key: 'times',
            width: 60,
            render: (text, record) => (
                <span>
                    {record?.sampleTest?.length} 
                </span>
            ),
        },
        // {
        //     title: '',
        //     dataIndex: 'location',
        //     key: 'location',
        //     width: 50,
        // },
        {
            title: '',
            dataIndex: 'result',
            key: 'result',
            width: 60,
            render:(text,record)=>(
                <Space size="middle">
                    { record?.currentState?.confirm ? "":"" }
                </Space>
            )
        },
        // {
        //     title: '',
        //     dataIndex: 'symptom',
        //     key: 'symptom',
        //     width: 50,
        // },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 100,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleSampleTestDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]

    return array
}