import { gql } from '@apollo/client'

export const GET_ALL_SAMPLE = gql`
query getPeopleForSampleTestWithPagination($page:Int!,$limit:Int!,$keyword:String, $startDate:DateTime, $endDate:DateTime){
    getPeopleForSampleTestWithPagination(page:$page,limit:$limit,keyword:$keyword, startDate:$startDate, endDate:$endDate){
        personalInfos{
          id
          firstName
          lastName
          patientId
          englishName
          idCard
          gender
          age
          tel
          village
          commune
          district
          province
          nationality
          vaccination{
            date
            times
            vaccineType
            vacinatedAt
          }
          sampleTest{
            id 
            date 
            times 
            testLocation 
            result 
            symptom 
            other 
            reasonForTesting 
            symptomStart 
            labFormCompletedBy 
            specimentType 
            laboratory 
            covidVariant 
            resultDate 
            testType
          }
          currentState{
            confirm
            confirmedAt
            recovered
            recoveredAt
            death
            deathAt
            covidVariant
            }
          affectedFrom{
            date
            patientName
            patientCode
            relation
            direct
            otherAffect
          }
          
        }
      paginator{
        slNo
        prev
        next
        perPage
        totalPosts
        totalPages
        currentPage
        hasPrevPage
        hasNextPage
        totalDocs
      }
    }
  }
`

