export const DELETE_SAMPLETEST = gql`
mutation deleteSampleTest($sampleTestId:ID!,$personalInfoId:ID!){
  deleteSampleTest(sampleTestId:$sampleTestId,personalInfoId:$personalInfoId){
    success
    message
  }
}

`

export const UPDATE_SAMPLE_TEST= gql`
mutation updateSampleTest(
  $personalInfoId:ID!,
  $sampleTestId:ID!,
  $covidVariant: String,
  $reasonForTesting: String,
  $date: DateTime,
  $times: Int,
  $testLocation: String,
  $result: Boolean,
  $symptom: String,
  $other: String,
  $symptomStart: DateTime,
  $labFormCompletedBy: String,
  $specimentType: String,
  $laboratory: String,
  $resultDate: DateTime,
  $testType:String
  ){
  updateSampleTest(personalInfoId:$personalInfoId,sampleTestId:$sampleTestId,sampleTest:{
    covidVariant:$covidVariant
    reasonForTesting:$reasonForTesting
    date:$date
    times:$times
    testLocation:$testLocation
    result:$result
    symptom:$symptom
    other:$other
    symptomStart:$symptomStart
    labFormCompletedBy:$labFormCompletedBy
    specimentType:$specimentType
    laboratory:$laboratory
    resultDate:$resultDate
    testType:$testType
  }){
    success
    message
  }
}
`

export const GET_SUM_SAMPLE = gql`
query getAllAllSampleTest{
  getAllAllSampleTest{
    all
    today
  }
}

`;