
import {GET_DATA_FOR_GRAP} from '../../graphql/dashboardAndReport'
import { useQuery } from "@apollo/client";
import React, { useState, useEffect } from "react";
import { Bar,Line } from "react-chartjs-2";
import numeral from "numeral";


function BarChart({ casesType }) {
  const [data, setData] = useState({});
  const {dat,refetch} = useQuery(GET_DATA_FOR_GRAP,{onCompleted:({getDataForGrap})=>{
    setData(getDataForGrap);
  }})

let caseLabel = data?.cases?.map(a=>a.x )
let recoveredLabel = data?.recovered?.map(a=>a.x )
let deathLabel = data?.deaths?.map(a=>a.x )
let newlabel = caseLabel?.concat(recoveredLabel,deathLabel)?.sort((a,b)=>new Date(a)-new Date(b))
let caseData = data?.cases?.map(a=>a.y )
let recoveredData = data?.recovered?.map(a=>a.y )
let deathData = data?.deaths?.map(a=>a.y )
console.log(caseData,"sg")

// const dd= ["dfds","dfd","hels","dgd"]
const va= data.cases

const datas = {
 
  labels: newlabel,
  datasets: [
    {
      label: "",
      data: caseData,
      fill: false,
      backgroundColor: "yellow",
      borderColor: "yellow"
    },
    {
      label: "",
      data: deathData,
      fill: false,
      backgroundColor: "red",
      borderColor: "red"
    },
    {
      label: "",
      data: recoveredData,
      fill: false,
      backgroundColor: "green",
      borderColor: "green"
    },
  ]
};
  return (
    <div style={{height:"400px"}}>
        <Line
        data={datas}
        options={{
          maintainAspectRatio: false,
        }}
        />
    </div>
  );
}

export default BarChart;