import React, { useContext, useState, useEffect } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, DatePicker,message } from 'antd'
import { ListSelect } from '../../../static/own-comp'
import { useMutation,useQuery } from '@apollo/client';
import {UPDATE_SAMPLE_TEST} from '../../../graphql/sample'
import moment from 'moment'
import { convertPersonalToSelect, convertLocationToSelect } from '../../../function/fn'
import AddLocation from '../../location/modal/addLocation'
import AddPeople from '../../people/modal/addPeople'

const {Option} = Select

export default function EditSampleTest({ open, setOpen, data, setRefetch,personalAllData, locationAllData  }) {

    let [form] = Form.useForm()

    const [openPersonal, setOpenPersonal] = useState(false)
    const [openLocation, setOpenLocation] = useState(false)

    const [locationId, setLocationId] = useState("")
    const [personalInfoId, setPersonalInfoId] = useState("")

    const [locationData, setLocationData] = useState({})
    const [personalInfoData, setPersonalInfoData] = useState({})

    const [positiveState,setPositiveState] = useState(false)

    const [updateSampleTest,{loading}] = useMutation(UPDATE_SAMPLE_TEST,{
        onCompleted:()=>{
            setRefetch()
            message.success("")
        }
    })

    console.log(data)

    useEffect(() => {
        if(data!== undefined){
            setPositiveState(data.result)
            form.setFieldsValue({
                ...data,
                date: data.date === null || data.date === undefined ? null : moment.parseZone(data.date),
                resultDate: data.resultDate === null || data.resultDate === undefined ? null :moment.parseZone(data.resultDate), 
                symptomStart: data.symptomStart === null || data.symptomStart === undefined ? null : moment.parseZone(data.symptomStart)
            })
        }
    }, [data])

    const onFinish = (values) => {
       
        console.log('Success:', {
            ...values,
            date: moment.parseZone(values.date).format(),
            resultDate:moment.parseZone(values.resultDate).format(),
            symptomStart:moment.parseZone(values.symptomStart).format(),
            times: parseInt(values.times),
        });

        updateSampleTest({
            variables:{
                ...values,
                date: values.date === null || values.date === undefined ? null : moment.parseZone(values.date).format(),
                resultDate: values.resultDate === null || values.resultDate === undefined ? null : moment.parseZone(values.resultDate).format(),
                symptomStart: values.symptomStart === null || values.symptomStart === undefined ? null : moment.parseZone(values.symptomStart).format(),
                times: parseInt(values.times),
                personalInfoId: data.personalInfoId,
                sampleTestId:data.id
            }
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToPeopleHospitalFn = (e) => {
        form.setFieldsValue({
            location: e
        });
    }

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
            personalInfoId: e
        });
        if (e === "new") {
            setOpenPersonal(true)
        }

        setPersonalInfoId(e)
    };

    const callbackPersonalInfo = (e) => {
        // console.log("test",e)
        if(e === ""){
            setPersonalInfoId(e)
            form.setFieldsValue({
                personalInfoId: null
            })
        }else {
            setPersonalInfoData(e)
            console.log(e)
            form.setFieldsValue({
                personalInfoName: e.lastName + " " + e.firstName
            })
        }
    }

    const setToLocationFn = (e) => {
        form.setFieldsValue({
            affectedLocationId: e
        });
        if (e === "new") {
            setOpenLocation(true)
        }

        setLocationId(e)
    };

    const callbackLocation = (e) => {
        // console.log("test",e)
        if(e === ""){
            setLocationId(e)
            form.setFieldsValue({
                affectedLocationId: null
            })
        }else {
            setLocationData(e)
            form.setFieldsValue({
                affectedLocationName: e.affectedLocationName
            })
        }
    }

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <AddPeople open={openPersonal} setOpen={setOpenPersonal} personalInfoId={personalInfoId} setPersonalInfoId={callbackPersonalInfo} personalInfoData={personalAllData}/>
            <AddLocation open={openLocation} setOpen={setOpenLocation} locationId={locationId} setLocationId={callbackLocation} />
            <Form
                form={form}
                name="addPeopleTest"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={ personalInfoId === "new" ? { span: 11, offset: 0 } : { span: 24, offset: 0 }}>
                        <Form.Item
                            name="personalInfoId"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={6} data={convertPersonalToSelect(personalAllData)} title="" setValue={setToPersonalInfoFn} />
                        </Form.Item>
                    </Col>

                    {
                        personalInfoId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="personalInfoName"
                                >
                                    <Input disabled={true} style={{backgroundColor: "white", color: "black"}} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{width:"100%"}}/>
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="resultDate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{width:"100%"}}/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                        name="testLocation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                        <Input placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                        name="testType"
                        // rules={[{ required: true, message: '!' }]}
                        >
                        <Select placeholder="" style={{ width: "100%" }}>
                            <Option value="RDT">RDT</Option>
                            <Option value="PCR">PCR</Option>
                            <Option value=""></Option>
                        </Select>
                        </Form.Item>
                    </Col>
                    
                    {/* <Col xs={24} md={ locationId === "new" ? { span: 11, offset: 0 } : { span: 24, offset: 0 }}>
                        <Form.Item
                            name="affectedLocationId"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={6} data={convertLocationToSelect(locationAllData)} title="" setValue={setToLocationFn} />
                        </Form.Item>
                    </Col>
                    {
                        locationId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="affectedLocationName"
                                >
                                    <Input disabled={true} style={{backgroundColor: "white", color: "black"}} />
                                </Form.Item>
                            </Col>
                        ) : null
                    } */}

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="reasonForTesting"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:2 }}>
                        <Form.Item
                            name="symptomStart"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{width:"100%"}}/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="specimentType"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:2 }}>
                        <Form.Item
                            name="laboratory"
                            // rules={[{ required: true, message: '!' }]}
                        >
                             <Select placeholder="" style={{ width: "100%" }}>
                                <Option value="IPC">IPC</Option>
                                <Option value="NIPH">NIPH</Option>
                                <Option value="PKML">PKML</Option>
                                <Option value="MKB">MKB</Option>
                                <Option value="BTB">BTB</Option>
                                <Option value="TKV">TKV</Option>
                                <Option value="SRH">SRH</Option>
                                <Option value="NBTC">NBTC</Option>
                                <Option value="SHV">SHV</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="result"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }} onChange={(e)=> setPositiveState(e)}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    {
                        positiveState === true ? (
                        <Col xs={24} md={{ span: 11, offset:2 }}>
                            <Form.Item
                                name="covidVariant"
                                // rules={[{ required: true, message: '!' }]}
                            >
                                {/* <Input placeholder="" /> */}
                                <Select placeholder="" style={{ width: "100%" }}>
                                <Option value="COVID-19">COVID-19</Option>
                                    <Option value="DELTA">DELTA</Option>
                                    <Option value="ALPHA">ALPHA</Option>
                                    <Option value=""></Option>
                                </Select>
                            </Form.Item>
                        </Col>
                        ):null}

                    <Col xs={24} md={positiveState === true ? {span:24} : {span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24}}>
                        <Form.Item
                            name="symptom"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>


                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"
                        
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24}}>
                        <Form.Item
                            name="labFormCompletedBy"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
