import React from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Divider } from 'antd'

export default function AddReportMoreData({open, setOpen, data, setData}) {
    let [form] = Form.useForm()

    const onFinish = (values) => {
        console.log('Success:', values);
      };
    
      const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
      };

    return (
        <Modal
            visible={open}
            okText=""
            cancelText=""
            onOk={() => onFinish()}
            onCancel={() => setOpen(false)}
            title=""
        >
            <Form
                name="addReportMoreData"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Divider>.</Divider>
                    <Col
                        xs={{span:11, offset:0}}
                    >
                        <Form.Item
                            name="dateOfBirth"
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:2}}
                    >
                        <Form.Item
                            name="social"
                        >
                            <Input placeholder=""/>
                        </Form.Item>
                    </Col>
                    <Divider>.</Divider>
                    <Col
                        xs={{span:11, offset:0}}
                    >
                        <Form.Item
                            name="dateOfArrived"
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:2}}
                    >
                        <Form.Item
                            name="comefrom"
                        >
                            <Input placeholder=""/>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:0}}
                    >
                        <Form.Item
                            name="target"
                        >
                            <Input placeholder=""/>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:2}}
                    >
                        <Form.Item
                            name="dateOfGoto"
                        >
                            <Input placeholder=""/>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:24, offset:0}}
                    >
                        <Form.Item
                            name="goto"
                        >
                            <Input placeholder=""/>
                        </Form.Item>
                    </Col>
                    <Divider>.</Divider>
                    <Col
                        xs={{span:24, offset:0}}
                    >
                        <Form.Item
                            name="other"
                        >
                            <Input placeholder=""/>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
